@font-face {
  font-family: 'Optima';
  src: url('../../assets/optima.woff2') format('woff2'),
    url('../../assets/optima.woff') format('woff'),
    url('../../assets/optima.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Optima';
  src: url('../../assets/optima-bold.woff2') format('woff2'),
    url('../../assets/optima-bold.woff') format('woff'),
    url('../../assets/optima-bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

@import '~font-awesome/css/font-awesome.css';
