@import '~font-awesome/scss/variables';
@import '~font-awesome/scss/mixins';
@import '../mixins';

// Page content which can be shifted to right by sidebar
.snap-content.snap-content {
  position: absolute;
  background-color: #fff;
  overflow: hidden;
}
.snap-content-scroller {
  height: 100%;
  overflow: auto;

  & > .ff-dimmer {
    z-index: 10;
  }
}

// Sidebar container
.snap-drawer {
  max-width: $sidebar-width;
  //border-right: 1px solid $sidebar-item-border-color;
}

// Draggable area for swiping sidebar from left
.snap-dragger {
  position: absolute;
  top: 0;
  left: 0;
  width: 16px;
  height: 100%;

  @media (min-width: $sidebar-breakpoint) {
    display: none;
  }
}

// Sidebar permanently visible
@media (min-width: $sidebar-breakpoint) {
  .sidebar-visible {
    .snap-content {
      transform: translate3d(0, 0, 0) !important;
      margin-left: $sidebar-width;
    }
    .snap-drawer {
      display: block !important;
    }
  }
}

// Sidebar logo
.sidebar-logo {
  display: flex;
  align-items: center;
  min-height: $header-banner-bar-size;
  padding: 10px;
  background-color: var(--logo-bg-color);

  img {
    object-fit: contain;
    max-width: 100%;
    max-height: 160px; // to fit banner height

    .inelastic-banner & {
      max-height: 130px;
    }
  }

  a, img {
    width: 100%; // upscale logo
  }
}

// Selectors block
.sidebar-selectors {
  position: sticky;
  top: -1px;
  width: 100%;
  z-index: 2;
  text-transform: uppercase;
  font-size: .8em;

  color: var(--sidebar-color);
  background-color: var(--sidebar-bg-color);

  .ff-control {
    justify-content: center;
    padding: 5px;
    border-radius: var(--panel-border-radius);
    text-transform: none;
    border: 1px solid $sidebar-group-border-color;
    background-color: #fff;

    option { // fix select content aligning in Firefox
      padding-top: 5px;
    }
  }

  .user-selector {
    dw-select-user {
      cursor: pointer;
    }

    ff-btn {
      margin: -7px -5px -5px 0;
    }

    .ff-btn {
      height: 100%;
      min-height: initial;
      align-items: center;
      justify-content: center;
      padding-right: 3px;
      box-shadow: none;
      color: var(--brand-color);
      background-color: transparent;
      border: 0;

      .fa {
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-color: #fff;
      }
    }
  }
}

// Sidebar selector
.sidebar-selector {
  display: block;
  padding: 7px 5px;
  border-top: 1px solid $sidebar-group-border-color;
  color: var(--sidebar-color);

  & > span:first-child {
    display: flex;
    margin-bottom: 5px;
  }
}

.sidebar-body {
  margin-top: 1px;
}

// Cloaking sidebar during page load
body:not(.snapjs-left) .sidebar {
  display: none;
}


/**
 * Group header and item common styles
 */
.sidebar-group-header, .sidebar-item {
  position: relative;
  display: block;
  margin: -1px 0 0 0;
  padding: 11px 10px;
  border: solid $sidebar-item-border-color;
  border-width: 1px 0;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 13px;
  line-height: 1;
  font-weight: normal;
  transition: all .1s;
  cursor: pointer;

  @include on-event(true) {
    text-decoration: none;
  }
}

/**
 * Item
 */
.sidebar-item {
  background-color: $sidebar-item-bg-color;
  color: $sidebar-item-color;
  border-color: $sidebar-item-border-color;

  &.disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  &:hover {
    background-color: $sidebar-item-hover-bg-color;
    color: $sidebar-item-hover-color;
    border-color: $sidebar-item-hover-border-color;
    z-index: 1;
  }

  &.active {
    background-color: var(--sidebar-item-active-bg-color);
    color: var(--text-on-brand-color);
    border-color: $sidebar-item-active-border-color;
  }
}

/**
 * Group header
 */
.sidebar-group-header {
  background-color: var(--sidebar-group-bg-color);
  color: $sidebar-group-color;
  border-color: $sidebar-group-border-color;
  transform: translateZ(0);

  &:hover {
    background-color: $sidebar-group-hover-bg-color;
    color: $sidebar-group-hover-color;
  }

  //Arrow
  &::after {
    @include fa-icon();
    content: '\f0da';
    position: absolute;
    top: 50%;
    right: 12px;
    font-size: 18px;
    transform: translateY(-50%) scale(2.2, .8);
    transition: all 0.1s;
    z-index: 1;
    clip-path: polygon(100% 0%, 100% 100%, 0 100%, 20% 50%, 0 0);
  }

  // Active badge
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    right: 5.5px;
    width: 21px;
    height: 21px;
    transform: translateY(-50%);
    background-color: var(--sidebar-item-active-bg-color);
    border-radius: 50%;
    box-shadow: 0 0 5px 1px rgba(255, 255, 255, 0.25);
    display: none;
  }

  &.open {
    &::after { // arrow rotation
      transform: translateY(-50%) rotate(90deg) scale(2.2, .8);
      right: 12.5px;
    }
  }

  &.active {
    background-color: $sidebar-group-active-bg-color;
    color: $sidebar-group-active-color;

    &::before { // active badge visibility
      display: block;
    }

    &::after {
      color: var(--text-on-brand-color);
    }
  }
}

// Collapsible items container
.sidebar-items {
  list-style: none;
  margin: 0;
  padding: 0;

  .ff-spin::before { // align sidebar item spin
    left: auto !important;
    right: 1px !important;
    z-index: 2;
  }
}
