// ### Header ###
.header {
  display: flex;

  max-width: $main-max-width;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  background-color: var(--header-bg-color);
}

.header-logo {
  flex: 0 0 250px;
  display: flex;
  align-items: center;
  padding: 1rem;
  background-color: var(--logo-bg-color);

  img {
    object-fit: contain;
    max-width: 100%;
    max-height: 130px;
  }

  a, img {
    width: 100%; // upscale logo
  }
}

.sidebar-visible {
  .header-logo {
    display: none;
  }
  .header-banner {
    height: 180px;
    max-height: 180px;

    &::before {
      height: $header-banner-bar-size;
    }
  }
}

.header-banner {
  flex: 1 1 auto;
  position: relative;
  max-height: 160px;

  &::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 30%;
    background-color: var(--header-bg-color);
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

// Setup -> White Label Settings -> Elastic Banner
.inelastic-banner {
  .header-banner {
    &::before {
      display: none;
    }

    img {
      object-fit: contain;
      object-position: left;
    }
  }

  &.sidebar-visible .header-banner {
    height: auto;

    & > img {
      position: absolute;
      top: 0;
    }
  }

  &.sidebar-hidden .header-banner {
    & > img {
      display: none;
      @media (min-width: $screen-sm) {
        display: block;
      }
    }
  }
}

.header-banner .logo-fake { // fake logo for inelastic banner height
  display: none;

  .sidebar-visible.inelastic-banner & {
    display: block;
    visibility: hidden;
  }
}


.hide-banner {
  .header-banner.header-banner {
    height: 60px;
  }

  &.inelastic-banner {
    .logo-fake.logo-fake {
      display: none;
    }

    .header-banner > img {
      height: 100%;
    }
  }
}

// ### Main ###
.main {
  max-width: $main-max-width;
  margin: 0 auto;

  @media (min-width: $collapse-sidebar-width) {
    padding: 0 1.75rem;
  }

  &, &.sidebar-hidden {
    padding: 0 1rem;
  }
}

// Narrow centered content when sidebar is hidden
.sidebar-hidden {
  .header, .main {
    max-width: $main-max-width-narrow;
  }
}


.snap-content {
  display: block;
}

.snap-content-wrapper {
  padding-bottom: 0;
  // Page content wrapper for adding sticky footer
  position: relative;
  min-height: 100%;
}
