/**
 * Badge
 */
.badge {
  position: relative;
  font-size: 0.9em;
  font-weight: bold;
  color: #fff;
  z-index: 0;

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 18px;
    height: 18px;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background-color: var(--gray-bg-color);
    z-index: -1;
  }

  &.primary::before {
    background-color: var(--black-bg-color);
  }
  &.info {
    color: var(--text-on-brand-color);
    &::before {
      background-color: var(--brand-color);
    }
  }
  &.danger::before {
    background-color: var(--danger-color);
  }
}
