.ff-control, .ff-label {
  min-height: $ff-control-min-height;
  padding: 0 $ff-control-padding;
  border-bottom: 1px solid $ff-control-border-color;
  border-radius: 0;

  &:focus {
    box-shadow: inset 0 -2px var(--brand-color);
  }

  &.ng-invalid {
    box-shadow: none; // prevent red frames on invalid inputs in Firefox
  }

  &.ng-invalid:focus {
    box-shadow: inset 0 -2px $color-danger;
    border-bottom-color: $ff-control-border-color;
  }
}

.standout {
  background: var(--brand-color);
  color: var(--text-on-brand-color);

  .ff-control, .ff-label {
    @extend .standout;
  }
}

.ff-no-frame {
  .ff-control, &.ff-control, .ff-label, &.ff-label {
    border-bottom-color: transparent;
    &.ng-invalid:focus {
      border-bottom-color: transparent;
    }
  }
}

@import './buttons';
@import './checkbox';
@import './radio';
@import './validation';
@import './datepicker';
@import './img';
