$ff-dp-bg-color: #fff;

$ff-dp-btn-bg: transparent;
$ff-dp-btn-border: 0;
$ff-dp-cal-bg: $ff-dp-bg-color;
$ff-dp-cal-border: 1px solid #f2f2f2;

.ff-datepicker {
  background: $ff-dp-bg-color;
  font-weight: normal;
}

ff-date button.fa {
  position: absolute;
  z-index: 2;
  top: 50%;
  right: 2px;
  transform: translateY(-50%);
  padding: $ff-control-padding;
  background: $ff-dp-bg-color;
  border: 0;
  color: inherit;
  transition: color 0.1s;

  @include on-event() {
    color: var(--brand-color);
    outline: none;
  }
}

// Disabled state for Open Datepicker button
ff-date {
  button:disabled {
    opacity: 0.3;
    background-color: $ff-dp-bg-color;
    color: #333 !important;
  }
}

#ff-dp-popup {
  position: absolute;
  max-width: 220px;
  min-width: 220px;
  font-size: 0.9rem;
  z-index: 20;
  border: 1px solid #ccc;
  box-shadow: 0 0 5px rgba(100, 100, 100, 0.5);
  outline: none;
}

ff-date #ff-dp-popup { // align by right side in ffDate directive
  right: 0;
}

.ff-dp-monthly.ff-dp-monthly {
  & > * {
    width: 32.75%;
  }
}

.ff-dp-day, .ff-dp-year {
  padding: 0.4em 0;
  color: $text-color;
  background: $ff-dp-cal-bg;
  border: $ff-dp-cal-border;
  transition: all .15s;

  &.dimmed {
    color: fade_out($text-color, .6);
  }

  &.current {
    color: #fff;
    background-color: var(--brand-color);
  }

  &:disabled {
    color: rgba(0, 0, 0, .2);
    background-color: #fcfcfc;
  }

  &.today {
    color: #fff;
    position: relative;
    z-index: 0;

    &::before {
      background-color: $color-danger;
      width: 21px;
      height: 21px;
      content: '';
      position: absolute;
      display: block;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
      border-radius: 50%;
      z-index: -1;
    }
  }

  &:hover {
    border-color: #ccc;
    z-index: 1;
  }
  &:focus {
    outline: 1px dashed var(--brand-color);
    outline-offset: -1px;
    z-index: 1;
  }

  // Immediately hide fading out elements of ngRepeat to prevent flashing
  &.ng-leave {
    display: none;
  }
}

// ff-datepicker header
.ff-dp-cal-header {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  color: $text-color;

  button {
    color: inherit;
    background: $ff-dp-btn-bg;
    border: $ff-dp-btn-border;

    &:focus {
      outline: none;
    }
  }

  // Month/Year block at center
  div {
    span { // Month span
      padding: 10px 0;
    }
    button { // Year button
      padding: 0;
      margin-left: 5px;
    }
  }
}

// Years selection
.ff-dp-years {
  display: flex;
  margin: 0 -4px 0 0; // Compensate .ff-dp-day border collapsing

  & > * {
    flex: 0 0 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: -1px -1px 0 0; // Border collapsing
  }
}

// Week days header
.ff-dp-cal-week {
  display: flex;

  & > * {
    flex: 0 0 14.2857%;
    display: flex;
    justify-content: center;
    color: var(--brand-color);
  }
}

// Calendar body
.ff-dp-cal-body {
  display: flex;
  flex-flow: row wrap;
  margin: 1px -6px 0 0; // Compensate .ff-dp-day border collapsing

  & > * {
    width: 14.2857%;
    margin: -1px -1px 0 0; // Border collapsing
  }
}
