.menu-button {
  @include fa-icon();
  content: '\f0c9';

  position: absolute;
  left: 0;
  top: 5px;
  font-size: 24px;
  width: 48px;
  height: 48px;
  cursor: pointer;
  z-index: 50;

  display: flex;
  align-items: center;
  justify-content: center;
  transform: scaleY(1.1);

  color: #fff;
  text-shadow: 0 0 1px #000, 0 0 1px #000, 0 0 1px #000, 0 0 1px #000, 0 0 1px #000, 0 0 1px #000; // be visible on white

  @media (min-width: $sidebar-breakpoint) {
    display: none;
  }
}
