.message-board {
  .msg-container {
    margin: .5rem 0;

    &:first-of-type {
      margin-top: 0;
    }
    &:last-of-type {
      margin-bottom: 0;
    }
  }

  .msg-meta {
    line-height: 1.2;
    margin-bottom: 2px;
  }

  .msg-ava {
    flex: 0 0 50px;
    margin: 0 .5rem 0 0;

    ff-img {
      width: 48px;
      height: 48px;
      margin-bottom: auto;
      overflow: hidden;

      img {
        border-radius: 50%;
      }
    }

    .fa {
      font-size: 48px;
      margin: 0 auto;
      opacity: 0.1;
    }
  }

  .msg-name {
    margin-right: 5px;
    font-size: .9em;
    font-weight: bold;
  }
  .msg-time {
    margin-right: 5px;
    font-size: .8em;
    font-weight: bold;
    opacity: .65;
  }
  .msg-date {
    font-size: .75em;
    opacity: .65;
  }

  more {
    color: var(--brand-color);
    text-transform: lowercase;
  }
}
