@import '~scss/mixins';

// buttons group
ff-btn-group {
  display: block;
  font-size: 0;
  text-align: right;
  align-items: center;

  fieldset {
    margin: -.25rem;
  }

  ff-btn {
    margin: .25rem;
  }

  // Block buttons group which is shared for block of controls (e.g. Edit/Remove in User Profile)
  // Section buttons group which is shared for section with blocks of controls (e.g. Add in User Profile)
  &.ff-block-buttons, &.ff-section-buttons {
    ff-btn {
      margin-left: 0;
      margin-right: 5px;
    }
  }
}

.buttons-bottom, .buttons-top {
  margin: .5rem 0;

  fieldset {
    margin: 0 -.5rem;
  }

  ff-btn {
    margin: .5rem;
  }
}

.buttons-top {
  margin: 0 0 .5rem 0;
}

.buttons-row {
  margin-left: .333rem;
}

// Section Buttons
.ff-section-buttons, .ff-block-buttons {
  margin: 0;
}

* > .ff-section-buttons, * > .ff-block-buttons {
  &:not(:first-child) {
    margin-top: 1rem;
  }

  &:not(:last-child) {
    margin-bottom: 1rem;
  }
}

// button wrapper
ff-btn {
  display: inline-block;
  font: normal .93333rem/1 var(--font-family-minor);
  font-weight: normal;
}

// button element
.ff-btn {
  display: flex;
  align-self: center;
  align-items: stretch;
  border: $button-border-width solid;
  background-color: transparent;
  text-transform: uppercase;

  & > span:first-of-type:not(.fa) { // margin between text and icon
    margin: 0 .333rem 0 0;
  }

  .fa.fa {
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 14px; // even number of pixels for correct centering icons
  }

  transition: all .05s;
  @include on-event() {
    outline: none;
    box-shadow: 0 0 1rem rgba(0, 0, 0, .3);
    transform: scale(1.05);
  }
  &:active, &:active:hover {
    box-shadow: 0 0 2rem rgba(0, 0, 0, .3);
    background-color: transparent;
  }
}

// back button
ff-btn[sense=back] .ff-btn {
  flex-direction: row-reverse; // icon before text
  & > span + span { // margin between text and icon
    margin: 0 .333rem 0 0;
  }
}


// favorite button should simulate disabled status
ff-btn[sense=favorite] button {
  pointer-events: none;
}

// button icon
.ff-btn-icon {
  .ff-btn {
    border: none !important;

    .fa {
      text-shadow: 0 0 1px #fff, 0 0 1px #fff, 0 0 2px #fff, 0 0 2px #fff;
      transition: all .1s ease;
    }
  }
}

// disabled
.ff-btn.disabled, .ff-btn[disabled], fieldset[disabled] .ff-btn {
  opacity: 0.3;
}

// standard size button
.btn-sm {
  padding: .6rem .666rem;
  border-radius: var(--btn-border-radius);
}

// small size button
.btn-xs {
  min-height: 28px;
  padding: .3rem .5rem;
  border-radius: calc(var(--btn-border-radius) - var(--btn-border-radius) / 4);
}


@mixin xs-btn-mixin() {
  .ff-btn {
    height: 30px;
    width: 30px;
    max-width: 30px;
    justify-content: stretch;

    &.ff-btn { // more weight to selector
      padding: 0;
    }

    .fa {
      width: 100%;

      &::before {
        display: block;
        transform: scale(1.3);
      }
    }
  }
  .ff-btn > *:not(.fa) {
    display: none;
  }
}


ff-btn[size=xs] { // always xs
  @include xs-btn-mixin();
}


//ff-btn[size=sm]:not(.ff-btn-full),
ff-btn-group:not(.ff-btn-full):not(.ff-section-buttons):not(.ff-block-buttons) ff-btn[size=sm]:not(.ff-btn-full)
{ // collapsed to xs
  @media (max-width: $screen-sm - 1px) {
    @include xs-btn-mixin();
  }
}

// Button text is not not collpased on mobile
.ff-btn-full .ff-btn > *:not(.fa) {
  display: inline;
}

// collapse buttons
ff-btn {
  &[sense=collapseBtn], &[sense=collapseBtnPriv], &[sense=collapse] {
    .fa {
      transition: all 0.1s;
    }
    &.collapsed .fa {
      transform: rotate(-90deg);
    }
  }

  &[sense=collapseBtn], &[sense=collapseBtnPriv] {
    .fa {
      font-size: 1.1em;
    }
  }

  &[sense=collapse] {
    .ff-btn {
      .fa {
        font-size: inherit;
        &::before {
          display: block;
          transform: scale(1.75);
        }
      }
    }
  }
}

// success button
.btn-success.btn-success {
  @include on-event(true) {
    color: var(--text-on-brand-color);
    background-color: var(--btn-success-color);
    border-color: var(--btn-success-color);
  }
}

// warning button
.btn-warning.btn-warning {
  @include on-event(true) {
    color: var(--btn-warning-color);
    border-color: var(--btn-warning-color);
  }
}

// primary button
.btn-primary {
  @include on-event(true) {
    color: $btn-primary-color;
    border-color: $btn-primary-color;
  }
}

// danger button
.btn-danger.btn-danger {
  @include on-event(true) {
    color: $btn-danger-color;
    border-color: $btn-danger-color;
  }
}

.btn-link.btn-link {
  text-transform: none;
  @include on-event(true) {
    padding: 0;
    color: $btn-primary-color;
    border-color: transparent;
    box-shadow: none;
    transform: none;
  }
  @include on-event() {
    text-decoration: underline;
  //  border-color: $btn-primary-color;
  }
  //border-color: transparent;
}
