.dw-invoice {
  display: block;
  flex-direction: column;

  .invoice-charges, .invoice-tax, .invoice-payment, .invoice-balance {
    @extend .em;
  }
}

// TODO: move it to correct place
#memberSignUpPurchase, #memberSignUpComplete, #simpleReservationPurchase, #simpleReservationComplete, #buyPassPurchase {
  .invoice-date-created, .invoice-added-by {
    display: none;
  }

  @media (max-width: 640px) {
    .invoice-description {
      display: none;
    }
  }

  @media (max-width: 560px) {
    .invoice-date {
      display: none;
    }
  }
}

dw-invoice-small {
  ff-header {
    padding: .5rem 0;
  }

  ff-row:last-child {
    .ff-label, .ff-control {
      border-bottom: 2px solid $text-color;
    }
  }
}
