.ff-control, .ff-label {
  padding: 0;
  border: 0;
  color: $text-color;
  background-color: transparent;
  font-size: 1rem;
}

.ff-btn {
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  background: none;

  & > * {
    vertical-align: baseline;
  }

  &:disabled, &:disabled:hover, fieldset:disabled ff-btn &, fieldset:disabled ff-btn &:hover {
    background-color: inherit;
  }

  span + .fa {
    margin: 0;
  }
}

label {
 color: inherit;
}

.sidebar {
  font-family: var(--font-family-minor);
  font-size: 15px;

  .ff-control {
    font-size: 15px;
    min-height: 36px;
  }
}

.ff-tip {
  font-family: var(--font-family-minor);
}
