ff-checkbox, ff-checkbox.ff-control {
  display: flex;
  align-items: center;
  min-height: calc(#{$ff-control-min-height} - 1px);

  input {
    position: absolute;
    margin: 0;
    opacity: 0;
  }

  i {
    display: inline-block;
    width: .8rem;
    min-width: .8rem;
    height: .8rem;
    min-height: .8rem;
    border: 1px solid;
    border-radius: 2px;
  }

  .cb-label {
    margin-left: .5rem;

    & > * {
      vertical-align: text-top;
    }
  }

  input:checked + i, checked + i, .checked + i {
    color: var(--brand-color);
    background-color: currentColor;
  }

  input:focus + i {
    color: var(--brand-color);
  }

  input[disabled] + i {
    color: #cbcccb;

    + .cb-label {
      opacity: .5;
    }
  }

  input.ng-invalid:focus + i {
    color: $color-danger;
  }

  &.ff-include-all { // include all checkbox which should be aligned with other checkboxes, so its label is clipped
    span {
      position: absolute; // prevent clipping checkbox label
      top: 0;
      left: 30px;
      font-size: .9rem;
      line-height: $ff-control-min-height;
      white-space: nowrap;
    }
  }
}
