// smooth collapse animation for ng-repeat and ng-if
.animate-collapse {
  &.ng-enter, &.ng-leave {
    overflow: hidden;
    transition: max-height 1s cubic-bezier(0.5, 0, 1, 0), opacity .5s ease-in;
  }
  &.ng-leave {
    transition: max-height 1s cubic-bezier(0, 1, 0, 1) -.5s, opacity .5s;
  }
  &.ng-enter, &.ng-leave.ng-leave-active {
    max-height: 0;
    opacity: 0;
  }
  &.ng-leave, &.ng-enter.ng-enter-active {
    max-height: 9999px;
    opacity: 1;
  }
}
