h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
  color: inherit;

  small, .small {
    font-weight: 400;
    line-height: 1;
    color: #777;
  }
}

h1, .h1, h2, .h2, h3, .h3 {
  margin-top: 1.333rem;
  margin-bottom: .666rem;

  small, .small {
    font-size: 65%;
  }
}

h4, .h4, h5, .h5, h6, .h6 {
  margin-top: .666rem;
  margin-bottom: .666rem;

  small, .small {
    font-size: 75%;
  }
}

h1, .h1 { font-size: 2.4rem; }
h2, .h2 { font-size: 2rem; }
h3, .h3 { font-size: 1.6rem; }
h4, .h4 { font-size: 1.2rem; }
h5, .h5 { font-size: 1rem; }
h6, .h6 { font-size: .8rem; }

// Body
p {
  margin: 0 0 .666rem;
}

.text-nowrap { white-space: nowrap; }


// Page Title
.page-title {
  margin: 1rem 0;
  padding: 0;
  border: 0;
  font: 300 1.6rem/1.1 var(--font-family-minor);
  text-align: left;
  text-transform: uppercase;
  color: inherit;

  @media (min-width: 480px) {
    font: 300 1.8667rem/1.1 var(--font-family-minor);
  }

  small {
    font-size: 1em;
    font-weight: inherit;
    color: inherit;
    text-transform: none;
  }

  .need-help {
    margin-top: .4rem;
    text-transform: none;
    font-size: .75em;
    color: var(--brand-color);

    a {
      color: inherit;
      text-decoration: none;
      @include on-event() {
        text-decoration: underline;
      }
    }
  }

  &.flex {
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    justify-content: space-between;
  }
}

//a {
a:not(.sidebar-item) { // TODO: remove on restyling sidebar
  color: $link-color;
  text-decoration: underline;
  transition: color $transition-duration;

  @include on-event() {
    color: var(--link-hover-color);
  }
}

.em {
  text-transform: uppercase;
  font-weight: bold;
}

.pre-description {
  white-space: pre-wrap;
  font-style: italic;

  .ff-static {
    align-items: flex-start;
    line-height: 1.5;
  }
}

.no-margin {
  margin: 0 !important;
}

.flex-wrap {
  flex-wrap: wrap;
}

.text-danger {
  color: $color-danger;
}
.text-brand {
  color: var(--brand-color);
}
.text-text {
  color: $text-color;
}

// Invisible
.invisible {
  visibility: hidden;
}
.zero-width {
  visibility: hidden;
  max-width: 0;
  margin-left: 0;
  margin-right: 0;
}
.zero-height {
  visibility: hidden;
  max-height: 0;
  margin-top: 0;
  margin-bottom: 0;
}

// Inline-block
i-block, .i-block {
  display: inline-block;
}
