@use 'sass:math';

// spinner aka progress indicator

$spin-size-sm: 20px;
$spin-size-md: 30px;

.ff-spin, ff-spin {
  position: relative;

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: math.div(-$spin-size-md, 2) 0 0 math.div(-$spin-size-md, 2);
    overflow: hidden;
    border-radius: 50%;
    width: $spin-size-md;
    height: $spin-size-md;
    border: 3px solid rgba(230,230,230,0.8);
    border-bottom-color: var(--brand-color);
    animation: spin1 1s infinite linear;
    z-index: -1;

    @keyframes spin1 {
      100% {
        transform: rotate(360deg);
      }
    }
  }

  &.ff-spin-sm::before { // small spin
    width: $spin-size-sm;
    height: $spin-size-sm;
    margin: math.div(-$spin-size-sm, 2) 0 0 math.div(-$spin-size-sm, 2);
  }
}

ff-spin::before {
  z-index: 0;
}

ff-btn.ff-spin { // button spin with dimming
  .btn {
    opacity: 1;
  }
  &::before { // spin over button and dimmer
    //@extend .ff-spin-sm::before;
    width: $spin-size-sm;
    height: $spin-size-sm;
    margin: math.div(-$spin-size-sm, 2) 0 0 math.div(-$spin-size-sm, 2);
    z-index: 1;
  }
  &::after { // button dimmer
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(ellipse at center, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 20%, rgba(255,255,255,0.5) 100%);
    border-radius: inherit;
  }
}

.ff-spin-placeholder { // placeholder with control-like height with spin inside
  display: block;
  width: 100%;
  height: $ff-control-min-height;
  &::before {
    z-index: 0;
  }
}

ff-search.ff-spin::before { // spin over ffSearch should be on top
	z-index: 3; // TODO: refactor ffSearch, get rid of this
}

/**
 * ffDim directive (dimmed element with loading progress indicator)
 */
.ff-dim {
  position: relative;
}

.ff-dimmer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(ellipse at center, rgba(255,255,255,0.7) 0%, rgba(255,255,255,0.7) 20%, rgba(255,255,255,0.5) 100%);
  border-radius: inherit;
  z-index: 2;

  .ff-dim-app > & { // top app dimmer between switching routes, enabled by $app.dim
    position: fixed;
    z-index: 100;
    bottom: auto;
    height: 100vh;
  }
}
