@use 'sass:math';

/**
 * ffBoard
 */
.ff-board, ff-board {
  display: block;
  margin: 0 -1rem 1rem -1rem;
  padding: 1rem;
  background-color: $ff-board-bg-color;
  border-radius: calc(var(--panel-border-radius) * 1.2);
}

ff-header, .ff-header, .ff-panel-header {
  display: block;
  line-height: 1.1;
  font-weight: bold;
  text-transform: uppercase;

  cell, .cell {
    display: block;
    padding: 0 .25rem;

    @media (max-width: $screen-md - 1px) {
      font-size: .9333rem;
    }
    @media (max-width: $screen-sm - 1px) {
      font-size: .8666rem;
    }
  }

  &.ff-spin::before { // right corner panel spin
    left: auto;
    right: .25rem;
  }
}

ff-header, .ff-header {
  margin: 0;
  padding: 0 0 .5rem 0;
  border-bottom: 2px solid;
}

/**
 * ffPanel
 */
.ff-panel {
  display: flex;
  flex-direction: column;
}

.ff-panel-header {
  margin: 0 0 .5rem 0;
  padding: 0 1rem;

  small {
    font-size: .9333em;
    font-weight: normal;
    font-style: italic;
    text-transform: none;

    req::after {
      top: .15em;
      font-size: 1.5em;
      line-height: 0;
    }
  }
}

.ff-panel-body {
  flex: auto;
  position: relative;
  padding: 1rem;
  background-color: $ff-panel-bg-color;
  box-shadow: 0 0 var(--panel-shadow-radius) rgba(0, 0, 0, var(--panel-shadow-opacity));
  border: 1px solid #bbb;
  border-radius: var(--panel-border-radius);
}

.ff-panel.dragover .ff-panel-body { // files are being dragged
  box-shadow: 0 0 2rem var(--brand-color);
}

.ff-panel .ff-panel {
  .ff-panel-header {
    margin: 0;
    padding: 0 0 .5rem 0;
    border-bottom: 2px solid;
  }

  .ff-panel-body {
    padding: 0;
    border-radius: 0;
    box-shadow: none;
    border: none;
  }
}

/**
 * ffPanelSpin
 */
.ff-panel-body {
  & > ff-spin:not(.ff-spin-placeholder), & > .ff-spin:not(.ff-spin-placeholder) {
    position: absolute;
    right: 15px;
    top: 15px;
    z-index: 1;

    &::before { // small spin
      $spin-size-sm: 20px;
      width: $spin-size-sm;
      height: $spin-size-sm;
      margin: math.div(-$spin-size-sm, 2) 0 0 math.div(-$spin-size-sm, 2);
    }
  }
}

/**
 * ffGrid
 */
.ff-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  & > * {
    flex-basis: 100%;
  }
}

.ff-gutter-v, .gutter-v {
  margin-bottom: -1.5rem;

  & > * {
    margin-bottom: 1.5rem;
  }
}

.ff-gutter-v-half, .gutter-v-half {
  margin-bottom: -.75rem;

  & > * {
    margin-bottom: .75rem;
  }
}

.ff-gutter-h, .gutter-h {
  margin-left: -.75rem;
  margin-right: -.75rem;
  & > * {
    padding-left: .75rem;
    padding-right: .75rem;
  }
}

.gutter-h-half.gutter-h-half {
  margin-left: -.375rem;
  margin-right: -.375rem;
  & > * {
    padding-left: .375rem;
    padding-right: .375rem;
  }
}

[flex] {
  min-width: 0;
}

.ff-clickable {
  &:hover {
    background-color: $ff-panel-bg-color;
  }
}

/**
 * One-line container for any child items
 */
.ff-row {
  display: flex;
  position: relative; // required for floating-toolbar

  > * {
    display: flex;
    flex: 1 1 1%; // equally stretch all child items, can be overridden by flex="1 1 10px" directive
    margin: 0;
  }
}

// Active state used in collapsible lists like Members / Member Usage
.ff-row[ff-click] {
  .ff-control {
    cursor: pointer;
  }

  &.active {
    .ff-control {
      @extend .standout;
    }
  }
}

/**
 * Multi-line container for any child items
 */
ff-col, .ff-col {
  flex-direction: column;
  display: flex;
}

/**
 * Aspect ratio sizer
 */
.aspect-ratio-sizer {
  display: grid;
}
.aspect-ratio-sizer > * {
  grid-area: 1 / 1 / 2 / 2;
}

/**
 * Show/Hide elements with media-queries
 */
.show-xl {
  @media (max-width: $screen-xl - 1px) {
    display: none;
  }
}
.show-lg {
  @media (max-width: $screen-lg - 1px) {
    display: none;
  }
}
.show-md {
  @media (max-width: $screen-md - 1px) {
    display: none;
  }
}
.show-sm {
  @media (max-width: $screen-sm - 1px) {
    display: none;
  }
}
.show-xs {
  @media (max-width: $screen-xs - 1px) {
    display: none;
  }
}

/**
 * Smaller font size on children content
 */
.ff-small {
  & > span {
    font-size: .9rem;
  }

  // ff-header, ff-panel-header children
  & > cell, & > .cell {
    @media (min-width: $screen-sm) {
      font-size: .9rem;
    }
  }
}

/**
 * Center content
 */
.ff-center, .ff-center > .ff-label, .ff-center > .ff-static, .ff-center input {
  justify-content: center;
  text-align: center;
}

/**
 * Align content to the right
 */
.ff-right, .ff-right > .ff-label, .ff-right > .ff-static, .ff-right input {
  justify-content: flex-end;
  text-align: right;
}

/**
 * Vertical margins
 */
.ff-tmargin {
  margin-top: 15px;
}
.ff-bmargin {
  margin-bottom: 15px;
}

/**
 * Add position: relative
 */
.ff-relative {
  position: relative;
}

/**
 * Align element to the top of container
 */
.ff-flex-start {
  align-self: flex-start;
}

/**
 * Clickable ff-row
 */
.ff-clickable {
  cursor: pointer;

  // Navigate icon
  .sense-navigate {
    opacity: 0.5;
    z-index: 2;
    font-size: 14px;
  }

  &:hover {
    filter: brightness(98%);
    z-index: 2;

    .sense-navigate {
      opacity: 1;
      color: var(--brand-color);

      &::before {
        transform: scale(1.2);
      }
    }
  }
}
